const Img = ({ match }) => {
  const [imageState, updateImageState] = useState([]);

  // Retrieves the list of items from the Express app
  let flag = false;
  const getImages = () => {
    fetch("/api/getImages")
      .then((img) => img.json())
      .then((res) => {
        console.log(res);
        updateImageState(res.files);
        if (!flag) {
          flag = true;
        }
      });
  };

  useEffect(() => {
    getImages();
  }, [imageState, flag]);

  function DisplayImages(imgList = imageState) {
    if (imgList.length === 0) {
      return <div>No Images</div>;
    } else {
      return (
        <ul>
          {imgList.map((imgUrl) => {
            return (
              <li>
                <img source={imgUrl}></img>
              </li>
            );
          })}
        </ul>
      );
    }
  }

  getImages();
};

export default Images;
